<template>
  <section class="exchange-confirmation">
    <header class="exchange-confirmation__header">
      Exchange Confirmation
    </header>
    <main class="exchange-confirmation__body">
      <ExchangeCoinInfo
        class="exchange-confirmation__body__info-item"
        :coin="sendCoin"
        label="You are sending"
        :rate="sendCoin?.rate"
        :sum="4.123"
      />
      <ExchangeCoinInfo
        class="exchange-confirmation__body__info-item"
        :coin="receiveCoin"
        label="You will receive"
        :rate="receiveCoin?.rate"
        :sum="0.0929345"
      />

      <div class="exchange-confirmation__body-estimate">
        <Tooltip
          text="Actual value depends on the time the transaction is processed"
          :show-arrow="false"
        >
          <div class="exchange-confirmation__body-estimate__content">
            <Icon
              class="exchange-confirmation__body-estimate__content-icon"
              name="clock"
            />
            <pre> Estimated Arrival: â‰ˆ <span class="exchange-confirmation__body-estimate__content-bold">2 - 40 minutes</span></pre>
          </div>
        </Tooltip>
      </div>
      <Input
        :label="'Payout address for ' + receiveCoin?.title + ':'"
        v-model="address"
        :error="error"
        error-text="Incorrect address format"
      />
    </main>
    <div class="exchange-confirmation__actions">
      <Checkbox
        class="exchange-confirmation__actions-checkbox"
        label="I've read and agree to the Switch Terms of Use and Privacy Policy"
      />
      <div class="exchange-confirmation__actions__confirmation">
        <Button
          class="exchange-confirmation__actions__confirmation-btn"
          type="primary"
        >Confirm</Button>
        <Button
          class="exchange-confirmation__actions__confirmation-btn"
          @click="goBack"
        >Back</Button>
      </div>
    </div>
  </section>
</template>

<script>
import ExchangeCoinInfo from '@/views/ExchangeConfirmation/ExchangeCoinInfo'
import { computed, ref, watch } from 'vue'
import Icon from '@/components/Icon/Icon'
import Input from '@/components/UIKit/Input'
import Checkbox from '@/components/UIKit/Checkbox'
import Button from '@/components/UIKit/Button'
import Tooltip from '@/components/UIKit/Tooltip'
import { useStore } from 'vuex'
import { useI18n } from 'vue-i18n'
import { useRouter } from 'vue-router'

export default {
  name: 'ExchangeConfirmation',
  components: { Tooltip, Button, Checkbox, Input, Icon, ExchangeCoinInfo },
  setup () {
    const store = useStore()
    const router = useRouter()
    const { t } = useI18n()

    const address = ref('3P3QsMVK89JBNqZQv5zMAKG8FK3kJM4rjt')
    const error = ref(false)

    const receiveCoin = computed(() => store.getters['exchange/getReceiveCoin'])
    const sendCoin = computed(() => store.getters['exchange/getSendCoin'])

    watch(address, (nv) => {
      error.value = nv.length !== 8
    })

    const hasHistory = () => window.history.length > 2
    const goBack = () => {
      if (hasHistory()) {
        router.go(-1)
      } else {
        router.push('/')
      }
    }
    return {
      t,
      receiveCoin,
      sendCoin,
      address,
      error,
      goBack
    }
  }
}
</script>

