<template>
  <div class="exchange-coin-info">
    <div class="exchange-coin-info__body">
      <div class="exchange-coin-info__body-label">
        {{ label }}
      </div>
      <div class="exchange-coin-info__body-sum">
        {{ sum }}
      </div>
      <div class="exchange-coin-info__body-rate">
        1 {{ coinLabel }} = ${{ rate }}
      </div>
    </div>
    <div class="exchange-coin-info__icon">
      <Icon
        :name="coin.icon + '-circle'"
        height="60px"
        width="60px"
      />
    </div>
  </div>
</template>

<script>
import Icon from '@/components/Icon/Icon'
import { computed } from 'vue'
export default {
  name: 'ExchangeCoinInfo',
  components: { Icon },
  props: {
    coin: {
      type: Object,
      default: () => {}
    },
    rate: {
      type: Number,
      default: 0
    },
    sum: {
      type: Number,
      default: 0
    },
    label: {
      type: String,
      default: ''
    }
  },
  setup (props) {
    const coinLabel = computed(() => props.coin?.key?.toUpperCase())

    return {
      coinLabel
    }
  }
}
</script>

