<template>
  <label class="checkbox">
    <input
      v-model="value"
      class="checkbox__field"
      type="checkbox"
    />
    <Icon class="checkbox__icon" :name="value ? 'checked' : 'unchecked'" />
    <span class="checkbox__label" v-html="label" />
  </label>
</template>

<script>
import Icon from '@/components/Icon/Icon'
import { ref } from 'vue'

export default {
  name: 'Checkbox',
  components: { Icon },
  props: {
    label: {
      type: String,
      require: true
    }
  },
  setup () {
    const value = ref(false)

    return {
      value
    }
  }
}
</script>

